import Cookie from '../utils/cookie';

const randomUserK = () =>{
    const randomC_userK = new Cookie('_shopify_random_user_key')
    const displayElement = document.getElementById('random-id-value')

    if (!randomC_userK.getCookie()){
        function randomID(length){
            let result = ""
            const characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
            const charactersLength = characters.length
            for ( var i = 0; i < length; i++ ) {
                result += characters.charAt(Math.floor(Math.random() * 
                charactersLength));
            }
            
            return result

        }
        const randomValue = randomID(5)
        
        if(displayElement){
            displayElement.innerHTML = randomValue
            randomC_userK.setCookie(randomValue, 50) 
        }
        

    } else {
        if(displayElement){
            displayElement.innerHTML =  randomC_userK.getCookie()
        }
        

    }




}

const init = () => {
    randomUserK();
}

export default init;