import Cookie from '../utils/cookie';



const timeACC = () => {
    const t_aCookie = new Cookie('_shopify_user_time_access')
    const getUserCookie = new Cookie('_shopify_random_user_key') 
    const getUserCookieValue = getUserCookie.getCookie()
    let displayElement = document.getElementById('time-access-value')
    let policyPages = ['terms-and-conditions', 'policy']
    let pathName = window.location.pathname 

    if(t_aCookie.getCookie() && !policyPages.some(v => pathName.includes(v))){
        const serverStatusElement = document.getElementById('server-status')

        const allElements = document.getElementById('MainContent')
        allElements.remove()

        const blockElement = document.querySelector('.ueq-ta_container')
        blockElement.style.display = "block"

        const userID = document.getElementById('ueq-ta_user')
        userID.innerHTML = getUserCookieValue

        displayElement.innerHTML = "00:00"
        displayElement.style.color = "#ee220c"

        serverStatusElement.innerHTML = "DISCONNECTED"
        serverStatusElement.style.color = "#ee220c"

        
    }
    
    if (!t_aCookie.getCookie() == true){
        let seconds;
        let temp

        let display = document.getElementById('time-access-value').innerHTML

    
        function countdown() {
            let time = document.getElementById('time-access-value').innerHTML
            let timeArray = time.split(':')
            seconds = timeToSeconds(timeArray);
            if (seconds == '') {
            temp = document.getElementById('time-access-value');
            temp.innerHTML = display;
            time = document.getElementById('time-access-value').innerHTML;
            timeArray = time.split(':')
            seconds = timeToSeconds(timeArray);
            }
            seconds--;
            temp = document.getElementById('time-access-value');
            temp.innerHTML = secondsToTime(seconds);
            let timeoutMyOswego = setTimeout(countdown, 1000);

            let valueForEndingSoon = document.getElementById("time-access-end").value

            if (secondsToTime(seconds) === valueForEndingSoon){
                displayElement.style.color = "#ee220c"
            }

            if (secondsToTime(seconds) === '00:00') {
            clearTimeout(timeoutMyOswego); 
            t_aCookie.setCookie(true, 1)
            window.location.reload()
            }
        }
        
        function timeToSeconds(timeArray) {
            let minutes = (timeArray[0] * 1);
            let seconds = (minutes * 60) + (timeArray[1] * 1);
            return seconds;
        }
        
        function secondsToTime(secs) {
            let hours = Math.floor(secs / (60 * 60));
            hours = hours < 10 ? '0' + hours : hours;
            let divisor_for_minutes = secs % (60 * 60);
            let minutes = Math.floor(divisor_for_minutes / 60);
            minutes = minutes < 10 ? '0' + minutes : minutes;
            let divisor_for_seconds = divisor_for_minutes % 60;
            let seconds = Math.ceil(divisor_for_seconds);
            seconds = seconds < 10 ? '0' + seconds : seconds;
        
            return `${minutes}:${seconds}`
        
        }
        countdown();
        }
    }

const init = () => {
    timeACC();
}

export default init;

