import Cookie from '../utils/cookie';

const ueqTerminal = () => {
    const t_aCookie = new Cookie('_shopify_user_time_access')
    const t_aCookieValue = t_aCookie.getCookie()

    if(!t_aCookieValue){
        const parentForModal = document.querySelector('.ueq-menu-container')
    const menuTitleButtons = document.querySelectorAll('.ueq-menu-title')
    const terminalTypeSpeed = document.getElementById('terminal-type-speed').value
    const terminalLineSpeed = document.getElementById('terminal-line-speed').value

    const cookieName = new Cookie('_shopify_random_user_key')
    const getUserKeyFromCookie = cookieName.getCookie()

    const createElement = (att) => {
        let newElement = document.createElement("div")
        newElement.setAttribute("id", "ueq-terminal")
        newElement.setAttribute("data-menu-terminal", att)

        const beforeElement = document.querySelector(`[data-menu='${att}']`)
        beforeElement.after(newElement)
    }

    const deleteElement = (att, type) => {
        if (type === "terminal"){
            const el = document.querySelector(`[data-menu-terminal='${att}']`)
            el.remove()
        } else if (type === "modal"){
            const el = document.querySelector(`#${att}-modal`)
            el.remove()
        }
    }

    const createModal = (att) => {
        const attLowerCase = att.toLowerCase()
        let inputId
        let inputTitleValue
        let inputTextAreaValue

        let newModalElement = document.createElement("div")

        // Get values from hidden input
        if (attLowerCase === "admin log"){
            inputId = "admin_log"
            inputTitleValue = document.getElementById(`${inputId}_modal_title`).value

            newModalElement.setAttribute("class", "ueq-modal-container")
            newModalElement.setAttribute("id", `${inputId}-modal`)
        } else {
            inputId = attLowerCase
            inputTitleValue = document.getElementById(`${inputId}_modal_title`).value
            inputTextAreaValue = document.getElementById(`${inputId}_modal_textarea`).value

            // Modal Container
            newModalElement.setAttribute("class", "ueq-modal-container")
            newModalElement.setAttribute("id", `${attLowerCase}-modal`)
        }

        // Modal wrapper
        let modalWrapper = document.createElement("div")
        modalWrapper.setAttribute("class", "ueq-modal-wrapper")
        newModalElement.appendChild(modalWrapper)

        // Modal header in wrapper + p & button
        let modalHeader = document.createElement("div")
        modalHeader.setAttribute("class", "ueq-modal-header")
        let pHeader = document.createElement("p")
        let pHeaderText = document.createTextNode(`${inputTitleValue}`)
        pHeader.appendChild(pHeaderText)
        modalHeader.appendChild(pHeader)
        let buttonHeader = document.createElement("button")
        buttonHeader.setAttribute("class", "close-modal")
        if (attLowerCase === "admin log"){
            buttonHeader.setAttribute("id", "admin_log")
        } else {    
            buttonHeader.setAttribute("id", `${attLowerCase}`)
        }
        
        buttonHeader.innerHTML = "X"
        modalHeader.appendChild(buttonHeader)

        modalWrapper.appendChild(modalHeader)

        // Modal body in wrapper + p
        let modalBody = document.createElement("div")
        modalBody.setAttribute("class", "ueq-modal-body")
        if (attLowerCase != "admin log"){

            if(attLowerCase === "contact"){
                let aBody = document.createElement("a")
                aBody.href = `mailto:${inputTextAreaValue}`
                aBody.textContent = `${inputTextAreaValue}`
                modalBody.appendChild(aBody)
            } else if(attLowerCase === "about"){
                let pBody = document.createElement("div")
                pBody.innerHTML = inputTextAreaValue
                modalBody.appendChild(pBody)
            }else {
                let pBody = document.createElement("p")
                let pbodyText = document.createTextNode(`${inputTextAreaValue}`)
                pBody.appendChild(pbodyText)
                modalBody.appendChild(pBody)
            }

            
        }

        if (attLowerCase === "admin log") {
            const getAdminLogLinks = document.querySelectorAll('#admin-log-link')
            getAdminLogLinks.forEach(element =>{
                const splitValueArray = element.value.split("-")

                let aBody = document.createElement("a")
                aBody.href = splitValueArray[1]
                aBody.textContent = `C:\\${getUserKeyFromCookie}\\${splitValueArray[0]}`
                aBody.setAttribute('class', 'aG-log-link')
                aBody.setAttribute('target', '_blank')
                modalBody.appendChild(aBody)
                 
            })
        }
        
        modalWrapper.appendChild(modalBody)

        // Return modal
        return newModalElement

    }

    const openTerminalAnimation = (title) => {
        const includesAbout = title.includes('ABOUT')
        const includesAdminLog = title.includes('ADMIN LOG')
        const titleWithoutChar = title.replace(/[^a-zA-Z ]/g, "")

        if(includesAbout && !document.querySelector(`[data-menu-terminal='ABOUT']`)){
            createElement(titleWithoutChar);
            
            if (document.querySelector(`[data-menu-terminal='ADMIN LOG']`)){
                deleteElement('ADMIN LOG', 'terminal')

                let terminal = new Termynal('#ueq-terminal', {
                    typeDelay: terminalTypeSpeed, 
                    lineDelay: terminalLineSpeed,
                    lineData: [
                        { value: 'ABOUT'},
                        { value: 'ACCESS TO RESTRICED DATA'},
                        { type: 'input',  prompt: `PASSENGER ID:`, value: `${getUserKeyFromCookie}`},
                        { type: 'input',  prompt: 'PASSWORD:', value: '**********'},
                        { value: 'SUCCESSFULLY LOGGED IN' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { type: 'input',  prompt: "$", value: 'ABOUT OF uE - JAN 23 - 19:16' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { value: 'ACCESS GRANTED'},
                    ],
                    callback: () =>{
                        let aboutModal = createModal(titleWithoutChar)
                        parentForModal.appendChild(aboutModal)

                        $('.close-modal').click(function(){
                            if(this.id === "about"){
                                deleteElement('ABOUT', 'terminal')
                                deleteElement(this.id, 'modal')
                            }
                        })
                    }
                })
            } else {
                let terminal = new Termynal('#ueq-terminal', {
                    typeDelay: terminalTypeSpeed, 
                    lineDelay: terminalLineSpeed,
                    lineData: [
                        { value: 'ABOUT'},
                        { value: 'ACCESS TO RESTRICED DATA'},
                        { type: 'input',  prompt: `PASSENGER ID:`, value: `${getUserKeyFromCookie}`},
                        { type: 'input',  prompt: 'PASSWORD:', value: '**********'},
                        { value: 'SUCCESSFULLY LOGGED IN' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { type: 'input',  prompt: "$", value: 'ABOUT OF uE - JAN 23 - 19:16' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { value: 'ACCESS GRANTED'},
                    ],
                    callback: () =>{
                        let aboutModal = createModal(titleWithoutChar)
                        parentForModal.appendChild(aboutModal)

                        $('.close-modal').click(function(){
                            if(this.id === "about"){
                                deleteElement('ABOUT', 'terminal')
                                deleteElement(this.id, 'modal')
                            }
                        })
                    }
                })
            }
        }

        if(includesAdminLog && !document.querySelector(`[data-menu-terminal='ADMIN LOG']`)){
            createElement(title.replace(/[^a-zA-Z ]/g, ""));
            
            if (document.querySelector(`[data-menu-terminal='ABOUT']`)){
                deleteElement('ABOUT', 'terminal')

                let terminal = new Termynal('#ueq-terminal', {
                    typeDelay: terminalTypeSpeed, 
                    lineDelay: terminalLineSpeed,
                    lineData: [
                        { value: 'ADMING_LOG'},
                        { value: 'ACCESS TO RESTRICED DATA'},
                        { type: 'input',  prompt: `PASSENGER ID:`, value: `${getUserKeyFromCookie}`},
                        { type: 'input',  prompt: 'PASSWORD:', value: '**********'},
                        { value: 'SUCCESSFULLY LOGGED IN' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { type: 'input',  prompt: "$", value: 'ADMING_LOG OF uE - JAN 23 - 19:16' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { value: 'ACCESS GRANTED'},
                    ],
                    callback: () =>{
                        let adminModal = createModal(titleWithoutChar)
                        parentForModal.appendChild(adminModal)

                        $('.close-modal').click(function(){
                            if(this.id === "admin_log"){
                                deleteElement('ADMIN LOG', 'terminal')
                                deleteElement(this.id, 'modal')
                            }
                        })
                    }
                })
            } else {
                let terminal = new Termynal('#ueq-terminal', {
                    typeDelay: terminalTypeSpeed, 
                    lineDelay: terminalLineSpeed,
                    lineData: [
                        { value: 'ADMING_LOG'},
                        { value: 'ACCESS TO RESTRICED DATA'},
                        { type: 'input',  prompt: `PASSENGER ID:`, value: `${getUserKeyFromCookie}`},
                        { type: 'input',  prompt: 'PASSWORD:', value: '**********'},
                        { value: 'SUCCESSFULLY LOGGED IN' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { type: 'input',  prompt: "$", value: 'ADMING_LOG OF uE - JAN 23 - 19:16' },
                        { type: 'progress', progressChar: '█', progressLength: 20 },
                        { value: 'ACCESS GRANTED'},
                    ],
                    callback: () =>{
                        let adminModal = createModal(titleWithoutChar)
                        parentForModal.appendChild(adminModal)

                        $('.close-modal').click(function(){
                            if(this.id === "admin_log"){
                                deleteElement('ADMIN LOG', 'terminal')
                                deleteElement(this.id, 'modal')
                            }
                        })
                    }
                })
            }
        }
    }

    menuTitleButtons.forEach(element => {
        element.addEventListener('click', ()=>{
            const elementValue = element.innerHTML
            const includesAbout = elementValue.includes('ABOUT')
            const includesAdminLog = elementValue.includes('ADMIN LOG')
            const includesShop = elementValue.includes('SHOP')
            const includesContact = elementValue.includes('CONTACT')
            const titleWithoutChar = elementValue.replace(/[^a-zA-Z ]/g, "")
            
            if (includesAbout){

                if(document.querySelector('#admin_log-modal')){
                    deleteElement('admin_log', 'modal')
                } else if (document.querySelector('#shop-modal')){
                    deleteElement('shop', 'modal')
                } else if (document.querySelector('#contact-modal')){
                    deleteElement('contact', 'modal')
                }   

                openTerminalAnimation(elementValue)
            } else if (includesAdminLog){

                if(document.querySelector('#about-modal')){
                    deleteElement('about', 'modal')
                } else if (document.querySelector('#shop-modal')){
                    deleteElement('shop', 'modal')
                } else if (document.querySelector('#contact-modal')){
                    deleteElement('contact', 'modal')
                }    

                openTerminalAnimation(elementValue)
            } else if (includesShop) {
                if(document.querySelector('#admin_log-modal')){
                    deleteElement('ADMIN LOG', 'terminal')
                    deleteElement('admin_log', 'modal')
                } else if (document.querySelector('#about-modal')){
                    deleteElement('ABOUT', 'terminal')
                    deleteElement('about', 'modal')
                } else if (document.querySelector('#contact-modal')){
                    deleteElement('contact', 'modal')
                } 

                if(!document.querySelector('#shop-modal') && !document.querySelector(`[data-menu-terminal='ABOUT']`) && !document.querySelector(`[data-menu-terminal='ADMIN LOG']`)){
                    let shopModal = createModal(titleWithoutChar)
                    parentForModal.appendChild(shopModal)
                }
                
            } else if (includesContact) {
                if(document.querySelector('#admin_log-modal')){
                    deleteElement('ADMIN LOG', 'terminal')
                    deleteElement('admin_log', 'modal')
                } else if (document.querySelector('#about-modal')){
                    deleteElement('ABOUT', 'terminal')
                    deleteElement('about', 'modal')
                } else if (document.querySelector('#shop-modal')){
                    deleteElement('shop', 'modal')
                } 

                if(!document.querySelector('#contact-modal') && !document.querySelector(`[data-menu-terminal='ABOUT']`) && !document.querySelector(`[data-menu-terminal='ADMIN LOG']`)){
                    let contactModal = createModal(titleWithoutChar)
                    parentForModal.appendChild(contactModal)
                }

            } else {
                console.log("PASSENGER DENIED");
            }

            $('.close-modal').click(function(){
                if(this.id != "admin_log" || this.id != "about"){
                    deleteElement(this.id, 'modal')
                }
            })
        })
    })
    }

    
}

const init = () => {
    ueqTerminal();
}

export default init;